import React from 'react';
import { Box, Paper, Grid, ButtonBase, Container, Link, CircularProgress } from '@material-ui/core';
import { Link as RouteLink } from 'react-router-dom';
import AppBar from '@/components/app-bar';
import { fetchCalfsList$ } from '@/api';
import { map } from 'rxjs/operators';
import useStyles from './styles';

const SearchCow: React.FC = props => {
    const classes = useStyles();
    const [List, setList] = React.useState([]);
    const [page, setPage] = React.useState(1);
    const [next, setNext] = React.useState(null);
    const [loading, setLoading] = React.useState(false);

    const loadMore = () => {
        if (next) {
            setPage(page + 1);
        }
    };

    React.useEffect(() => {
        setLoading(true);
        const ApiInst = fetchCalfsList$({page, page_size: 10}).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next])
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setList(List.concat(data[0]));
                setNext(data[1]);
                setLoading(false);
            }
        );
        return () =>  ApiInst.unsubscribe();
    }, [page]);

    return (
        <Box className={classes.root}>
            <AppBar title="查找牛只" />
            {
                List.map((item: any, i) => (
                    <Link component={RouteLink} to={`/cowDetail/${item.id}`}  key={`cow-item-${i}`}>
                        <Paper className={classes.paper} square>
                            <Grid container>
                                <Grid item xs={3} container justify="center" alignItems="center">
                                    <Container>
                                        <ButtonBase>
                                            <img className={classes.img} alt="complex" src={require('../../assets/images/default.jpg')} />
                                        </ButtonBase>
                                    </Container>
                                </Grid>
                                <Grid item xs>
                                    <div className={classes.infoItem}>
                                        <span className={classes.spanLeft}>牛号:</span>
                                        <span>{item.eartag}</span>
                                    </div>
                                    <div className={classes.infoItem}>
                                        <span className={classes.spanLeft}>所属牛舎:</span>
                                        <span>{item.pen_name}</span>
                                    </div>
                                    <div className={classes.infoItem}>
                                        <span className={classes.spanLeft}>类型:</span>
                                        <span>{item.cbrd || '暂无'}</span>
                                    </div>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Link>
                ))
            }
            <Choose>
                <When condition={loading}>
                    <Box display="flex" justifyContent="center" alignItems="center" className="text-center" marginTop="20px" fontSize="12px">
                        <CircularProgress size={15} className={classes.circularProgress} />正在加载中...
                    </Box>
                </When>
                <Otherwise>
                    <Box className="text-center" marginTop="20px" onClick={() => loadMore()} fontSize="12px">
                        {next ? '点击加载更多' : '没有更多了'}
                    </Box>
                </Otherwise>
            </Choose>
        </Box>
    )
}
export default SearchCow;