import React from 'react';
import { RouteComponentProps } from 'react-router-dom';
import {
    Table,
    TableHead,
    TableBody,
    TableRow,
    TableCell,
    Box,
    Paper
} from '@material-ui/core';
import {
    makeStyles
} from '@material-ui/styles';
import clsx from 'clsx';
import AppBar from '@/components/app-bar';

interface SceneReportListItem {
    name: string;
    date: string;
    type: string;
    id: string | number;
}
const createData = (name: string, date: string, type: string, id: string | number) => {
    return { name, date, type, id }
}
const SceneReportList: SceneReportListItem[] = [
    createData('风清扬', '2019-03-10', '乳头评分', 1),
    createData('任我行', '2019-03-10', '行走评分', 2),
    createData('风清扬', '2019-03-10', '巡棚评分', 3),
    createData('任我行', '2019-03-10', '体况评分', 4)
];
const useStyles = makeStyles((theme: any) => ({
    root: {
        paddingTop: 48
    },
    tableHeader: {
        color: theme.palette.primary.main
    }
}))
const SceneReport: React.FC<RouteComponentProps> = props => {
    const classes = useStyles();

    return (
        <Box className={classes.root}>
            <AppBar title="现场报告-清单" />
            <Paper square>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell className={classes.tableHeader} align="center">人员</TableCell>
                            <TableCell className={classes.tableHeader} align="center">日期</TableCell>
                            <TableCell className={classes.tableHeader} align="center">类型</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {SceneReportList.map(SceneReportListItem => (
                            <TableRow key={SceneReportListItem.id} onClick={() => props.history.push(`/sceneReportDetail/${SceneReportListItem.id}`)}>
                                <TableCell align="center">{SceneReportListItem.name}</TableCell>
                                <TableCell align="center">{SceneReportListItem.date}</TableCell>
                                <TableCell align="center">{SceneReportListItem.type}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </Paper>
        </Box>
    )
};
export default SceneReport;